import React, { useState, useEffect } from 'react';
import './App.css'
import Counter from './components/Counter';
import Navbar from './components/Navbar';

function App() {
  let [counterState, setCounterState] = useState(
    [
      {
        value: 0,
      },
      {
        value: 0,
      },
      {
        value: 0,
      },
      {
        value: 0,
      },
    ]
  )
  let [totalitems, setTotalItems] = useState(0);
  let [minusdisabled, setminusdisabled] = useState(true);

  let valueIncrementHandler = (arrayposition) => {
    let newCounterState = counterState;
    newCounterState[arrayposition].value++;
    totalHandler();
    setminusdisabled(false);
    setCounterState((counterState) => [...newCounterState])
  }

  let valueDecrementHandler = (arrayposition) => {
    let newCounterState = counterState;
    if (newCounterState[arrayposition].value > 0) {
      newCounterState[arrayposition].value--
    }
    if (newCounterState[arrayposition].value === 0) {
      setminusdisabled(true);
    }
    totalHandler();
    setCounterState((counterState) => [...newCounterState])
  }

  let deleteHandler = (arrayposition) => {
    let newCounterState = counterState;
    newCounterState.splice(arrayposition, 1);
    totalHandler();
    setCounterState((counterState) => [...newCounterState])
  }

  let totalHandler = () => {
    let currentItems = 0;
    counterState.map(item => {
      currentItems += item.value;
    })

    console.log(currentItems);
    setTotalItems(currentItems);
  }

  let resetHandler = () => {
    setCounterState(
      [
        {
          value: 0,
          plusenabled: false,
          minusenabled: true,
          visible: true,
        },
        {
          value: 0,
          plusenabled: false,
          minusenabled: true,
          visible: true,
        },
        {
          value: 0,
          plusenabled: false,
          minusenabled: true,
          visible: true,
        },
        {
          value: 0,
          plusenabled: false,
          minusenabled: true,
          visible: true,
        },
      ]
    )
  }

  return (
    <div className="container-fluid">
      <Navbar total={totalitems} />
      <Counter state={counterState} incrementHandler={valueIncrementHandler} decrementHandler={valueDecrementHandler} deleteHandler={deleteHandler} resetHandler={resetHandler} plusdisabled={false} minusdisabled={minusdisabled} />
    </div>
  );
}

export default App;
