import React from 'react'


const Counter = ({ state, incrementHandler, decrementHandler, deleteHandler, resetHandler, setTotal, plusdisabled, minusdisabled }) => {
    return (
        <div className="row mt-2">
            <div className="col-lg-6 m-2">
                <button className="btn btn-success m-2" onClick={resetHandler}>
                    <i className="fas fa-sync-alt"></i>
                </button>
                {state.map((item, index) => {
                    let className = " badge-warning m-2";
                    if (item.value > 0) {
                        className = " badge-primary m-2";
                    }
                    return (
                        <div className="row" key={index}>
                            <div className="col-lg-2">
                                <span className={"badge" + className} style={{ fontSize: "23px" }}>{item.value === 0 ? "Zero" : item.value}</span>
                            </div>
                            <div className="col-lg-1">
                                <button disabled={plusdisabled} className="btn btn-secondary m-2" onClick={() => incrementHandler(index)}><i className="fas fa-plus-circle"></i></button>
                            </div>
                            <div className="col-lg-1">
                                <button disabled={minusdisabled} className="btn btn-info m-2" onClick={() => decrementHandler(index)} ><i className="fas fa-minus-circle"></i></button>
                            </div>
                            <div className="col-lg-1">
                                <button className="btn btn-danger m-2" onClick={() => deleteHandler(index)}><i className="fas fa-trash"></i></button>
                            </div>
                        </div>
                    )
                })}
            </div>
        </div>
    )
}

export default Counter
